import React from 'react';
import gameSettings from '../../settings/game';
import tools from '../../settings/tools';
import env from '../../settings/env';
import Tool from '../../entity/Tool';
import Action from '../../entity/Action';
import Ending from '../../entity/Ending';
import PostOrgasm from '../../entity/PostOrgasm';
import Speed from '../../entity/Speed';
import Randomizer from '../../utils/Randomizer';
import { gameAction, gameEnding, gamePostOrgasm } from '../../utils/types';
// import Webcam from '../Webcam/Webcam';
import PornEmbed from '../PornEmbed/PornEmbed';
import Time from '../../utils/Time';
import SuperMath from '../../utils/SuperMath';
import { AppState } from '../../App';

export interface GameProps extends AppState { }

export interface GameState {
	isInGame: boolean,
	isPlaying: boolean,
	isEnding: boolean,
	isOverlayShown: boolean,
	currentSpeed: Speed,
	currentAction?: gameAction,
	postOrgasm?: any, // @TODO
	nextSpeed?: Speed,
	nextAction?: gameAction,
	remainingTime: number,
	totalTime: number,
	pastTotalTime: number,
	embedSource?: string,
	stats: any,
	gameSpeed: number,
	seed: string,
}

const BASE_STATE: GameState = {
	isInGame: false,
	isPlaying: false,
	isEnding: false,
	isOverlayShown: true,
	currentSpeed: gameSettings.speed.gentle,
	currentAction: undefined,
	postOrgasm: undefined,
	nextSpeed: undefined,
	nextAction: undefined,
	remainingTime: 0,
	totalTime: 0,
	pastTotalTime: 0,
	embedSource: 'https://www.pornhub.com/view_video.php?viewkey=ph5c12ab26a8b82',
	stats: {},
	gameSpeed: 1,
	seed: Randomizer.seed,
};

class Game extends React.Component<GameProps, GameState> {
	protected _tickCount: number = 0;
	protected _ticker?: any;
	protected _pornEmbed?: any = React.createRef();

	public state: GameState = BASE_STATE;

	public componentWillUnmount() {
		if (this._ticker) {
			clearInterval(this._ticker);
		}
	}

	protected get tools(): Tool[] {
		return this.props.tools
			.map(option => tools[option])
			.filter(postOrgasm => postOrgasm)
	}

	protected get actions(): Action[] {
		return this.props.actions
			.map(option => gameSettings.actions[option])
			.filter(postOrgasm => postOrgasm)
	}

	protected get endings(): Ending[] {
		return this.props.endings
			.map(option => gameSettings.endings[option])
			.filter(postOrgasm => postOrgasm)
	}

	protected get postOrgasms(): PostOrgasm[] {
		return this.props.postOrgasms
			.map(option => gameSettings.postOrgasms[option])
			.filter(postOrgasm => postOrgasm)
	}

	protected get uniqueActions(): gameAction[] {
		return this.actions.reduce<gameAction[]>((uniqueList, action) => {
			if (action.tools) {
				action.tools.forEach(tool => {
					uniqueList.push({
						text: action.actionText(tool.text),
						chance: action.chance,
						singleAction: action.singleAction,
					});
				})
			} else {
				uniqueList.push({
					text: action.actionText(''),
					chance: action.chance,
					singleAction: action.singleAction,
				});
			}

			return uniqueList;
		}, []);
	}

	protected get uniqueEndings(): gameEnding[] {
		return this.endings.reduce<gameEnding[]>((uniqueList, ending) => {
			if (ending.tools) {
				ending.tools.forEach(tool => {
					uniqueList.push({
						text: ending.actionText(tool.text),
						chance: ending.chance,
						canPostOrgasm: ending.canPostOrgasm,
					});
				})
			} else {
				uniqueList.push({
					text: ending.actionText(''),
					chance: ending.chance,
					canPostOrgasm: ending.canPostOrgasm,
				});
			}

			return uniqueList;
		}, []);
	}

	protected get uniquePostOrgasm(): gamePostOrgasm[] {
		return this.postOrgasms.map(postOrgasm => ({
			text: postOrgasm.actionText(''),
			chance: postOrgasm.chance,
		}));
	}

	protected get canPlay(): boolean {
		return this.uniqueActions.length > 0 && this.uniqueEndings.length > 0;
	}

	protected get gameSpeed(): number {
		if (!env.debug) {
			return 1;
		}

		return this.state.gameSpeed;
	}

	protected get tickSpeed(): number {
		const bps = this.state.currentSpeed.bps;

		if (bps === 0) {
			return 1;
		}

		if (bps < 1) {
			return bps;
		}

		return 1 / bps;
	}

	protected get totalTime(): string {
		const timeObject = Time.secondsToTime(Math.ceil(this.state.totalTime + this.state.pastTotalTime));

		return Time.hmsToClock(timeObject, { m: false });
	}

	protected get remainingTime(): number {
		return Math.ceil(this.state.remainingTime);
	}

	protected get animationSpeed(): string {
		if (!this.state.isPlaying || this.state.currentSpeed.bps === 0) {
			return '0ms';
		}
		return `${(1000 / this.gameSpeed) / this.state.currentSpeed.bps}ms`;
	}

	protected get currentSpeedBoxColor(): string {
		switch (this.state.currentSpeed) {
			case gameSettings.speed.edge:
			case gameSettings.speed.extreme:
				return 'bg-red-600';

			case gameSettings.speed.hard:
				return 'bg-yellow-500';

			case gameSettings.speed.medium:
				return 'bg-green-500';

			case gameSettings.speed.slow:
				return 'bg-blue-500';

			case gameSettings.speed.gentle:
				return 'bg-purple-500';

			default:
				return 'bg-black';
		}
	}

	protected get nextSpeedTextColor(): string {
		switch (this.state.nextSpeed) {
			case gameSettings.speed.edge:
			case gameSettings.speed.extreme:
				return 'text-red-600';

			case gameSettings.speed.hard:
				return 'text-yellow-500';

			case gameSettings.speed.medium:
				return 'text-green-500';

			case gameSettings.speed.slow:
				return 'text-blue-500';

			case gameSettings.speed.gentle:
				return 'text-purple-500';

			default:
				return 'text-white';
		}
	}

	protected play = () => {
		if (!this.state.isInGame) {
			this.setState(this.pickAction(this.state));
		}

		if (!this._ticker) {
			this.tick();
		}

		this.setState({ isInGame: true, isPlaying: true });
	}

	protected pause = () => {
		if (this._ticker){
			this.stopTick();
		}

		this.setState({ isPlaying: false });
	}

	protected stop = () => {
		if (this._ticker){
			this.stopTick();
		}

		Randomizer.resetRandom();

		this.setState({
			...BASE_STATE,
			embedSource: this.state.embedSource,
		});
	}

	protected toggleOverlay = () => {
		this.setState(prevState => ({ isOverlayShown: !prevState.isOverlayShown }));
	}

	protected getTime = (seconds: number): number => {
		return seconds * 1000; // Converts in milliseconds
	}

	protected setSeed = () => {
		Randomizer.seed = this.state.seed;

		this.setState({ seed: Randomizer.seed });
	}

	protected pickAction = (state: GameState): any => {
		type speedFilter = { whitelist?: Speed[], blacklist?: Speed[] };

		const getSpeedsList = (filter: speedFilter = {}): Speed[] => {
			if (filter.whitelist?.length) {
				return filter.whitelist;
			}

			if (filter.blacklist?.length) {
				return Object.values(gameSettings.speed)
					.filter(speed => !filter.blacklist?.includes(speed));
			}

			return Object.values(gameSettings.speed);
		};

		const getActionsList = (speed: Speed): gameAction[] => {
			if (speed === gameSettings.speed.cum) {
				return this.uniqueEndings as any;
			}

			return this.uniqueActions.filter(action => {
				return speed === gameSettings.speed.none
					? action.singleAction
					: !action.singleAction;
			});
		};

		const pickSpeed = (filter: speedFilter = {}): Speed => Randomizer.pickOneWithChance(
			getSpeedsList(filter).map(speed => [
				speed,
				speed.chance(this.getTime(state.totalTime))
			]),
		);

		const pickAction = (speed: Speed) => Randomizer.pickOneWithChance(
			getActionsList(speed).map(action => [
				action,
				action.chance(this.getTime(state.totalTime)),
			]),
		);

		const pickPostOrgasm = () => Randomizer.pickOneWithChance(
			this.postOrgasms.map(action => [
				action,
				action.chance(this.getTime(state.totalTime)),
			]),
		);

		let currentSpeed, currentAction, nextSpeed, nextAction;
		let stats = {};

		currentSpeed = state.nextSpeed ? state.nextSpeed : BASE_STATE.currentSpeed;

		currentAction = state.nextAction
			? state.nextAction
			: pickAction(currentSpeed);

		nextSpeed = pickSpeed({
			whitelist: currentSpeed.followUpWhitelist,
			blacklist: currentSpeed.followUpBlacklist || [currentSpeed]
		});

		stats = Randomizer.getChanceStatistics(
			getSpeedsList({
				whitelist: currentSpeed.followUpWhitelist,
				blacklist: currentSpeed.followUpBlacklist || [currentSpeed]
			}).map(speed => [
				speed,
				speed.chance(this.getTime(state.totalTime))
			]),
		);

		nextAction = pickAction(nextSpeed);

		if (!nextAction) {
			nextSpeed = pickSpeed({
				whitelist: currentSpeed.followUpWhitelist,
				blacklist: [...(currentSpeed.followUpBlacklist || [currentSpeed]), gameSettings.speed.none]
			});

			stats = Randomizer.getChanceStatistics(
				getSpeedsList({
					whitelist: currentSpeed.followUpWhitelist,
					blacklist: [...(currentSpeed.followUpBlacklist || [currentSpeed]), gameSettings.speed.none]
				}).map(speed => [
					speed,
					speed.chance(this.getTime(state.totalTime))
				]),
			);

			nextAction = pickAction(nextSpeed);
		}

		if (currentAction === nextAction && currentSpeed === nextSpeed) { // Reroll
			return this.pickAction(state);
		}

		const [minDuration, maxDuration] = currentSpeed.duration(state.totalTime);

		const remainingTime = Randomizer.int(maxDuration, minDuration);

		const isEnding = currentSpeed === gameSettings.speed.cum;
		const shouldPostOrgasm = isEnding && true;

		const postOrgasm = shouldPostOrgasm ? pickPostOrgasm() : null;

		return {
			currentAction,
			currentSpeed,
			nextAction,
			nextSpeed,
			remainingTime,
			isEnding,
			postOrgasm,
			stats,
		};
	}

	protected tick = (): void => {
		if (this.state.remainingTime - this.tickSpeed <= 0 && this.state.isEnding) {
			// @TODO postOrgasms.secondRound - Reset game but keep seed entropy + totalTime
			//
			// if (this.state.postOrgasm === gameSettings.postOrgasms.secondRound) {
			// 	this.setState(prevState => {
			// 		const actionState = this.pickAction(prevState);
			//
			// 		return {
			// 			isEnding: false,
			// 			totalTime: 0,
			// 			pastTotalTime: prevState.totalTime + 1,
			// 			currentAction: actionState.currentAction,
			// 			nextAction: actionState.nextAction,
			// 			currentSpeed: actionState.currentSpeed,
			// 			nextSpeed: actionState.nextSpeed,
			// 			remainingTime: actionState.remainingTime,
			// 			postOrgasm: actionState.postOrgasm,
			// 			stats: actionState.stats,
			// 		};
			// 	});
			//
			// 	return;
			// }

			this.pause();

			this.setState(prevState => ({
				totalTime: prevState.totalTime + 1,
				remainingTime: 0,
			}));

			return;
		}

		this.setState<any>(prevState => {
			const nextTickRemainingTime = prevState.remainingTime - this.tickSpeed;

			if (nextTickRemainingTime <= 0) {
				const actionState = this.pickAction(prevState);

				return {
					isEnding: actionState.isEnding,
					currentAction: actionState.currentAction,
					nextAction: actionState.nextAction,
					currentSpeed: actionState.currentSpeed,
					nextSpeed: actionState.nextSpeed,
					remainingTime: actionState.remainingTime,
					totalTime: prevState.totalTime + this.tickSpeed,
					postOrgasm: actionState.postOrgasm,
					stats: actionState.stats,
				};
			}

			return {
				remainingTime: nextTickRemainingTime,
				totalTime: prevState.totalTime + this.tickSpeed,
			};
		}, () => {
			if (this.shouldBeep()) {
				this.beep();
			}
		});

		this._ticker = setTimeout(this.tick, 1000 * this.tickSpeed / this.gameSpeed);
	}

	protected stopTick = () => {
		clearTimeout(this._ticker);

		this._ticker = undefined;
	}

	protected shouldBeep = (): boolean => {
		this._tickCount++;

		if (this.tickSpeed * this._tickCount >= 1 / this.state.currentSpeed.bps) {
			this._tickCount = 0;
			return true;
		}

		return false;
	}

	protected beep = (): void => {
		const sound = new Audio("data:audio/wav;base64,");

		sound.play();
	}

	public render() {
		if (!this.canPlay) {
			return null;
		}

		const stats = env.debug ? (
			<div className={'mb-5'}>
				<h3 className={'text-xl bold'}>Statistic</h3>
				<ul className={'flex flex-wrap -m-1 py-2'}>
					{Object.values(gameSettings.speed).map((value, i) => {
						return (
							<li key={i} className={'w-1/6 p-1'}>
								<span className={'w-24'}>{`${value.text}: `}</span>
								<span>{`${SuperMath.toPercent(this.state.stats[value.text]) || 0}%`}</span>
							</li>
						)
					})}
				</ul>
			</div>
		) : null;

		const actionsLists = (
			<div className={'mb-5'}>
				<h3 className={'text-xl bold'}>Unique actions</h3>
				<ul className={'flex flex-wrap py-2 -m-2'}>
					{this.uniqueActions.map((action, i) => (
						<li key={i} className={'p-2'}>{action.text}</li>
					))}
				</ul>
			</div>
		);

		const endingsLists = (
			<div className={'mb-5'}>
				<h3 className={'text-xl bold'}>Unique endings</h3>
				<ul className={'flex flex-wrap py-2'}>
					{this.uniqueEndings.map((action, i) => (
						<li key={i}>{action.text}</li>
					))}
				</ul>
			</div>
		);

		const overlay = this.canPlay ? (
			<div className={[
				'transition absolute w-full h-full pointer-events-none',
				!this.state.isOverlayShown ? 'opacity-0 visibility-none' : '',
			].join(' ')}>
				<div className={'w-full h-full p-3'}>
					<div className={'relative w-full h-full'}>

						<div className={'absolute p-3 top-0 left-0 bg-black bg-opacity-70'}>
							<div className={'text-md bold'}>{this.totalTime}</div>
						</div>

						{this.state.currentAction && (
							<div className={'absolute p-3 bottom-0 left-0 bg-black bg-opacity-70'}>
								{!this.state.isEnding && this.state.nextAction && this.state.nextSpeed && (
									<div className={'text-sm bold'}>
										{this.state.nextSpeed !== gameSettings.speed.stop && (
											<span>{`Up next: ${this.state.nextAction.text} `}</span>
										)}
										{this.state.nextSpeed !== gameSettings.speed.none && (
											<span className={this.nextSpeedTextColor}>{`(${this.state.nextSpeed.text})`}</span>
										)}
									</div>
								)}

								{this.state.isEnding && this.state.postOrgasm && (
									<div className={'text-sm bold'}>
										{this.state.postOrgasm.text}
									</div>
								)}

								<div className={'text-2xl bold'}>{this.state.currentAction.text}</div>
							</div>
						)}

						<div className={['absolute bottom-0 right-0 w-24 h-24 bg-opacity-70 overflow-hidden', this.currentSpeedBoxColor].join(' ')}>
							<div className={'relative flex flex-col justify-center items-center w-full h-full'}>
								<div
									className={'absolute w-full h-full bg-gray-100 bg-opacity-30 animate-bounce'}
									style={{ animationDuration: this.animationSpeed }}
								/>
								{this.state.currentSpeed !== gameSettings.speed.none && (
									<div className={'z-10 text-md'}>{this.state.currentSpeed.text}</div>
								)}
								<div className={'z-10 text-2xl bold'}>{this.remainingTime}</div>
							</div>
						</div>

					</div>
				</div>
			</div>
		) : null;

		return (
			<div>
				<div className={'flex -m-2 py-2'}>
					<div className={'p-2 w-3/4'}>
						<input
							className={'px-3 text-black w-full'}
							type={'text'}
							value={this.state.seed}
							placeholder={'Insert custom game seed'}
							onChange={e => this.setState({ seed: e.target.value })}
						/>
					</div>
					<div className={'p-2'}>
						<button
							className={[
								'transition px-3 border border-white rounded',
								Randomizer.seed !== this.state.seed ? 'bg-white text-black' : '',
							].join(' ')}
							onClick={this.setSeed}
						>
							<span>Change seed</span>
						</button>
					</div>
				</div>

				<div className={'flex -m-2 py-2'}>
					<div className={'p-2 w-3/4'}>
						<input
							className={'px-3 text-black w-full'}
							type={'text'}
							value={this.state.embedSource}
							placeholder={'Insert video link or id: ph:ph1a2b3c4d5f6g7 | xvid:01234567 | https://www.pornhub.com/view_video.php?viewkey=ph1a2b3c4d5f6g7 | https://www.xvideos.com/video01234567/abc_xyz'}
							onChange={e => this.setState({ embedSource: e.target.value })}
						/>
					</div>
					<div className={'p-2'}>
						<button
							className={'px-3 border border-white rounded'}
							onClick={() => this._pornEmbed.current?.setSource(this.state.embedSource)}
						>
							<span>Change video</span>
						</button>
					</div>
				</div>

				<div className={'flex -m-2 py-2'}>
					<div className={'p-2'}>
						<button className={'px-3 border border-white rounded'} onClick={this.play}>Play</button>
					</div>
					<div className={'p-2'}>
						<button className={'px-3 border border-white rounded'} onClick={this.pause}>Pause</button>
					</div>
					<div className={'p-2'}>
						<button className={'px-3 border border-white rounded'} onClick={this.stop}>Stop</button>
					</div>
					<div className={'p-2'}>
						<button
							className={[
								'transition px-3 rounded',
								this.state.isOverlayShown ? 'bg-white text-black' : ''
							].join(' ')}
							onClick={this.toggleOverlay}
						>
							Toggle overlay
						</button>
					</div>
					{env.debug && (
						<div className={'p-2 flex justify-center items-center'}>
							<div className={'mr-2'}>
								<span>Game speed: </span>
								<span className={'inline-block w-8 font-bold'}>{`${this.gameSpeed}x`}</span>
							</div>
							<input
								style={{ width: '300px' }}
								type={'range'}
								min={1}
								max={50}
								value={this.gameSpeed}
								onChange={(e) => this.setState({ gameSpeed: parseInt(e.target.value) })}
							/>
						</div>
					)}
				</div>

				<div className={'relative bg-black w-full h-0'} style={{ paddingBottom: `${(9/16) * 100}%` }}>

					<div className={'absolute w-full h-full'}>
						{/*<Webcam className={'object-cover w-full h-full'} />*/}
						<PornEmbed
							ref={this._pornEmbed}
							className={'object-cover w-full h-full'}
							onMount={() => {
								this._pornEmbed.current?.setSource(this.state.embedSource)
							}}
						/>
					</div>

					{overlay}

				</div>

				<div className={'mt-24'}>
					{stats}

					{actionsLists}

					{endingsLists}
				</div>

				<div className={'mt-24'}>
					<h2 className={'text-xl bold'}>Feedback</h2>
					<div className={'bg-gray-100'}>
						<iframe
							className={'w-full h-screen'}
							src={'https://docs.google.com/forms/d/e/1FAIpQLSddzBoiVNQjP0tN4xGhVmV60OLcz_054iuJ5nKrpTp8nCWaFw/viewform?embedded=true'}
							frameBorder={0}
							marginHeight={0}
							marginWidth={0}
						>
							Loading
						</iframe>
					</div>
				</div>
			</div>
		);
	}

}

export default Game;
