import React from 'react';
import Radio, { option } from '../Radio/Radio';

export interface SelectMultipleProps {
	name: string,
	options: option[],
	selected?: string,
	onChange: (selected: string) => void,
}

class SelectOne extends React.Component<SelectMultipleProps> {
	protected isChecked = (option: string) => {
		return this.props.selected === option;
	}

	public render() {
		const choices = this.props.options.map(option => (
			<div key={option.value} className={'w-1/2 sm:w-1/3 md:w-1/6'}>
				<Radio
					name={this.props.name}
					label={option.label}
					checked={this.isChecked(option.value)}
					onChange={() => this.props.onChange(option.value)}
				/>
			</div>
		));

		return (
			<div className={'flex flex-wrap'}>
				{choices}
			</div>
		);
	}
}

export default SelectOne;
