import React from 'react';
import Checkbox, { option } from '../Checkbox/Checkbox';

export interface SelectMultipleProps {
	name: string,
	options: option[],
	selected: string[],
	onChange: (selected: string[]) => void,
}

class SelectMultiple extends React.Component<SelectMultipleProps> {
	protected select = (option: string) => {
		this.onChange([...this.props.selected, option])
	}

	protected unselect = (option: string) => {
		this.onChange(this.props.selected.filter(selected => selected !== option))
	}

	protected onChange = (selected: string[]) => {
		this.props.onChange(selected);
	}

	protected isChecked = (option: string) => {
		return this.props.selected.some(selected => selected === option)
	};

	public render() {
		const choices = this.props.options.map(option => (
			<div key={option.value} className={'w-1/2 sm:w-1/3 md:w-1/6'}>
				<Checkbox
					name={this.props.name}
					label={option.label}
					checked={this.isChecked(option.value)}
					onChange={(e) => e.target.checked ? this.select(option.value) : this.unselect(option.value)}
				/>
			</div>
		));

		return (
			<div className={'flex flex-wrap'}>
				{choices}
			</div>
		);
	}
}

export default SelectMultiple;
